<template>
  <div class="db-layout" :class="{'mr-200':!dbRightBarVisible}">

    <Header :class="{'mr-200':!dbRightBarVisible}"/>
    
    <div class="db-layout__inner">
      <DbSidebar :isHide="dbSidebarVisible" />
      <div
        class="db-side"
        :class="{'hide': dbSidebarVisible}"
      >
        <RoutesBlock />
        <slot />
      </div>
    </div>

    <DbRightbar
      @close="TOGGLE_DB_RIGHTBAR"
      :isHide="dbRightBarVisible"
    />

    <transition name="modal">
      <DbModal
        v-if="dbModalVisible"
        @close="TOGGLE_DB_MODAL"
      >
        <div class="d-flex flex-column align-items-center p-5">
          <img
            class="my-2"
            src="@/assets/img/logo.png"
            width="150"
          />
          <div class="mb-5 text-center">The Admin Template For Perfectionist</div>
          <div class="form-group">
            <input type="text" class="form-control" id="newBusinessAlias" placeholder="Enter your alias">
          </div>
          <div class="form-group">
            <input type="text" class="form-control w-100" id="newBusinessName" placeholder="Enter your business name">
          </div>
          <button style="background-color: #0866C6;" type="submit" class="btn btn-block text-white" id="add-business-btn">Create Business</button>
        </div>
      </DbModal>
    </transition>

  </div>
</template>

<script>

import { mapState, mapMutations } from 'vuex';

export default {
  name: 'dashboardLayout',
  components: {
    Header: () => import('@/components/layout/dashboard/header'),
    RoutesBlock: () => import('@/components/layout/dashboard/routesBlock'),
    DbRightbar: () => import('@/components/layout/dashboard/rightBar'),
    DbSidebar: () => import('@/components/layout/dashboard/sidebar'),
    DbModal: () => import('@/components/layout/modal'),
  },
  methods: {
    ...mapMutations([
      'TOGGLE_DB_RIGHTBAR',
      'TOGGLE_DB_MODAL'
    ]),
  },
  computed: {
    ...mapState([
      'dbSidebarVisible',
      'dbRightBarVisible',
      'dbModalVisible'
    ])
  },
  async created() {
    
  }
}
</script>

<style lang="scss">
  .db-layout {
    transition: 0.5s ease margin;
  }
  .db-side {
    transition: 0.3s all ease;
    margin-left: 230px;
    &.hide {
      margin-left: 60px;
    }
    @media (max-width: 768px) {
      div & {
        margin: 0;
      }

      .db-layout &.hide {
        margin: 0;
      }
    }
  }
  .db-layout__inner {
    margin-top: 60px;
  }

  .mr-200 {
    margin-right: 200px;
  }
</style>